table {
  width: 100%;
}

.example-container tr td:last-child,
.example-container tr th:last-child {
  max-width: 150px;
  width: 100px;
  white-space: nowrap;
  text-align: left;
}

th.mat-header-cell {
  background: #0072ce;
  padding: 0 10px;
  white-space: nowrap;
}

table tr th:first-child {
  border-radius: 5px 0 0 0px !important;
}

table tr th:last-child {
  border-radius: 0px 5px 0 0px !important;
}

.main-inner {
  margin: 0 auto;
  width: 90%;
}

.sliderwrapper {
  text-align: center;
  margin: 0;
  padding: 0;
  position: relative;
}

.align-indicator {
  justify-content: end !important;
  position: relative;
}

td.mat-cell:last-of-type,
td.mat-footer-cell:last-of-type,
th.mat-header-cell:last-of-type {
  text-align: left;
}

.col {
  min-width: 0;
  max-width: 100%;
  padding: 0;
  font-size: 13px !important;
}

.colLink {
  min-width: 0;
  max-width: 100%;
  padding: 0;
  font-size: 13px !important;
  color: #1072ce;
  cursor: pointer;
}

.test-note {
  cursor: pointer;
  margin-left: 28px;
}

::ng-deep .mat-tooltip {
  max-width: 100% !important;
  background: white !important;
  color: black !important;
  border: 1px solid #ccc !important;
}

.mat-form-field-appearance-outline .mat-form-field-flex {
  padding: 0 !important;
}

.cust-width {
  width: 68%;
}

.dateouter {
  width: 75%;
}

.clsGreen {
  color: green;
}

.clsRed {
  color: red;
}

.indi-con {
  position: relative;
  display: inline-block;
  width: 100%;
}

.indicator {
  min-width: 32px;
  font-size: 10px;
  text-align: revert;
  position: relative;
  left: 13px;
  top: 2px;
  text-decoration: underline;
}

.col-sm-4 {
  flex: 0 0 33.333333%;
  max-width: 31.333333%;
  padding-right: 0;
}

.range .mat-form-field-flex {
  padding-left: 2px;
  padding-right: 1px;
}

.range input.mat-input-element {
  font-size: 13px;
}

.lstrow {
  background: #ed8b00;
  color: #fff;
  padding: 12px 10px;
  border-radius: 0px 0px 5px 5px;
}

.lstrow span {
  width: 100px;
  text-align: center;
}

.resultdata {
  background: #f5fbf7;
  padding: 4px;
  text-align: center;
  border-radius: 7px;
  width: 42px;
  display: block;
  font-size: 12px;
}

.mat-progress-bar {
  margin-right: 1px;
  height: 9px;
}

.patient-table tr th:nth-of-type(4),
.patient-table tr td:nth-of-type(4) {
  width: 170px;
}

.sliderwrapper {
  margin-bottom: 8px;
}

#print-btn {
  line-height: 34px;
  border-radius: 50% !important;
  height: 45px !important;
  width: 45px !important;
  background: #1072ce;
  z-index: 9;
}

#print-btn .mat-icon {
  color: #fff;
}

.backdate-outer {
  width: 85%;
  margin: 0 auto;
}

.rightarrow {
  position: absolute;
  right: 10px;
  top: 43%;
  transition: revert;
  transform: rotate(180deg);
  cursor: pointer;
}

.leftarrow {
  position: absolute;
  left: 10px;
  top: 43%;
  cursor: pointer;
}

.rightarrow.disable-arrow,
.leftarrow.disable-arrow {
  filter: grayscale(100%);
}

.trail-text {
  position: absolute;
  bottom: -8px;
  background: white;
  width: 151px;
  text-indent: -99999px;
}

/* Toggle  */

.btn-toggle:before {
  content: attr(data-graph);
  left: -5.5rem;
}

.btn-toggle:after {
  content: attr(data-trends);
  right: -4.5rem;
  opacity: 0.5;
}

.t-5 {
  height: 92px;
}

.t-4 {
  height: 56px;
}

.t-1 {
  margin-top: -37px;
}

#chartContainer {
  width: 90% !important;
  margin: 0 auto;
}

.trend {
  color: #ff6400;
  font-family: "gothambook", sans-serif;
  margin-top: 14px;
  font-size: 15px;
}

.chart-head {
  color: #ff6400;
  font-family: "gothambook", sans-serif;
  font-size: 15px;
  margin-left: 41px;
  margin-bottom: 30px;
  margin-top: 26px;
  z-index: 99999999;
  position: relative;
}

.btn-toggle.active:after {
  opacity: 1 !important;
}

.clr-date {
  position: absolute;
  right: 0;
  bottom: -16px;
  font-size: 11px;
  cursor: pointer;
  text-decoration: underline;
  color: #1072ce;
  font-family: "GothamNarrowBold", sans-serif;
}

hr {
  margin-top: 1.6rem !important;
}

.t-4-d-none {
  height: 14px;
}

.t-5-d-none {
  height: 92px;
}

.tfive-text .col {
  min-width: 40% !important;
  max-width: 100%;
  margin-bottom: 2px;
}

.t-head {
  font-family: "GothamNarrowBold", sans-serif;
}

.cmt-head {
  font-size: 17px;
  text-transform: uppercase;
  font-family: "GothamNarrowBold", sans-serif;
  font-weight: bold;
}

.c-point {
  cursor: pointer;
}

.follow_patient {
  margin-top: -7px;
}

.ana_result .col-sm-3 {
  margin-bottom: 20px;
}

.mat-slide-toggle {
  margin: 0px 0 0 9px;
  position: relative;
  top: 5px;
}

table {
  width: 100%;
}

tr.example-detail-row {
  height: 0;
}

tr.example-element-row:not(.example-expanded-row):hover {
  background: whitesmoke;
}

tr.example-element-row:not(.example-expanded-row):active {
  background: #efefef;
}

.example-element-row td {
  border-bottom-width: 0;
}

.example-element-detail {
  overflow: hidden;
  display: flex;
}

.example-element-diagram {
  min-width: 80px;
  border: 2px solid black;
  padding: 8px;
  font-weight: lighter;
  margin: 8px 0;
  height: 104px;
}

.example-element-symbol {
  font-weight: bold;
  font-size: 40px;
  line-height: normal;
}

.example-element-description {
  padding: 16px;
}

.example-element-description-attribution {
  opacity: 0.5;
}

.signs {
  font-weight: bolder;
  font-size: large;
}

table {
  width: 100%;
}

tr.student-detail-row {
  height: 0;
}

tr.student-element-row:not(.student-expanded-row):hover {
  background: whitesmoke;
}

tr.student-element-row:not(.student-expanded-row):active {
  background: #efefef;
}

.student-element-row td {
  border-bottom-width: 0;
  padding: 0 10px;
}

.student-element-detail {
  overflow: hidden;
  display: flex;
}

.student-element-diagram {
  min-width: 80px;
  border: 2px solid black;
  padding: 8px;
  font-weight: lighter;
  margin: 8px 0;
  height: 104px;
}

.prebtn {
  position: absolute;
  background: #fff;
  border: none;
  top: 4px;
  left: -4px;
}

.nxtbtn {
  position: absolute;
  background: #fff;
  border: none;
  top: 4px;
  right: 0px;
}

.pink {
  background: #ffeee3;
  display: flex;
  height: 48px;
  width: 91px;
  padding-left: 3px;
  align-items: center;
}

.review_head {
  color: #182b70;
  font-family: "GothamNarrowBold", sans-serif;
}

.review_subhead {
  color: #182b70;
  font-family: "GothamNarrowBold", sans-serif;
  font-size: 13px;
}

.text-block {
  border: 1px solid #99999969;
  padding: 10px;
  border-radius: 8px;
  max-height: 340px;
  overflow: auto;
  width: 97%;
}

.backlink {
  cursor: pointer;
  color: #2478c7 !important;
  text-decoration: underline;
  font-size: 14px;
}

td.mat-cell:first-of-type,
td.mat-footer-cell:first-of-type,
th.mat-header-cell:first-of-type {
  padding-left: 10px !important;
}

.left_arrow {
  position: absolute;
  left: 7px;
  top: 7px;
  z-index: 999;
}

.right_arrow {
  position: absolute;
  right: 9px;
  top: 7px;
  z-index: 999;
}

.date_block {
  width: 105px;
  padding: 0px 12px 0 0;
  white-space: normal;
  display: block;
}

.sticky-top {
  position: sticky;
  top: 6px;
}

.test_info_icon {
  vertical-align: middle !important;
  padding-right: 5px;
  padding-left: 0 !important;
  margin-left: 0;
}

.headtxt {
  cursor: pointer;
  width: 91%;
  float: right;
}

.flt-lft {
  float: left !important;
}

@media screen and (max-width: 767px) {
  .test-note {
    margin-left: 0;
  }

  .accesspart td.mat-cell:first-of-type {
    padding-left: 18px !important;
  }

  .accesspart td.mat-cell:last-of-type {
    padding-right: inherit;
  }

  td.mat-cel:first-of-type,
  td.mat-footer-cel:first-of-type,
  th.mat-header-cell:first-of-type {
    min-width: 155px;
  }

  .headtxt {
    cursor: pointer;
    width: 70%;
    float: right;
  }
}

.note-header {
  background: #0072ce;
  padding: 12px 14px;
  color: #fff;
  border-radius: 10px 10px 0px 0px;
  font-size: 14px;
}